.login {
  width: 100%;
  height: 100vh;
  background-color: #f8f8f8;
  display: flex;
  align-items: center;
  justify-content: center;

  .form-group {
    margin: 0.8rem 0.5rem 0rem 0rem;
    flex: 1;

    .input-border {
      height: 36px;
      background-color: #fff;
      border: 1px solid #d0d0d0;
      border-radius: 5px;
      padding-right: 15px;
      padding-left: 10px;
    }
  }
  .form-btn {
    display: flex;
    justify-content: center;
    .btn {
      margin: 1.5rem 0.5rem;
      border-radius: 5px;
      border: none;
      background-color: #185abc;
      color: #fff;
      padding: 10px 30px;
      cursor: pointer;
      font-size: 20px;
    }
  }
}
